/************************************************\
* Button Style                                   *
\************************************************/
@mixin button-style($color, $background-color: transparent, $border-color: null, $opacity: .25) {

    $border-color: $color !default;

    margin: 0 10px 5px 0;
    border: 2px solid $border-color;
    background-color: $background-color;
    display: inline-block;
    border-radius: 1px;
    line-height: 1;
    transition: background-color 0.25s, color 0.25s, border 0.25s ease-in-out;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        @if ($background-color = transparent) {
            background-color: rgba($border-color, $opacity);
        } @else {
            background-color: rgba($background-color, $opacity);
        }
    }

    a {
        color: $color;
        text-decoration: none;
        text-transform: uppercase;
        font-family: $font_two;
        padding: 15px 25px 14px;
        line-height: 1;
        font-size: 1.0em;
        margin: 0;
        display: inline-block;
        font-weight: 500;
        text-align: center;

        &:hover {
            text-decoration: none;
        }
    }

    @content;
}

/************************************************\
* Fancy Link                                     *
\************************************************/
@mixin fancy-link($color: #000) {
    text-decoration: none;
    color: $color;
    font-weight: bold;
    border-bottom: 1px solid $color;

    &:hover {
        text-decoration: none;
        color: $color;
    }

    @content;
}

/************************************************\
* Hub Button                                     *
\************************************************/
@mixin hub_button($active : false) {

    background: linear-gradient(0deg, rgba(36, 32, 33, 1) 0%, rgba(87, 93, 98, 0.95) 50%);
    line-height: 1;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    position: relative;
    z-index: 1;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;


    @media screen and (max-width: 768px) {
        border-radius: 0;
    }

    &:before {
        position: absolute;
        content: "";
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        background: linear-gradient(0deg, rgba(4, 62, 118, 1) 0%, rgba(16, 110, 163, .95) 50%);
        z-index: -1;
        transition: opacity 0.2s ease-in-out;

        @media screen and (max-width: 768px) {
            border-radius: 0;
        }

        @if($active == false){
            opacity: 0;
        }
    }

    &:hover {
        &:before {
            opacity: 1;
        }
    }

    a {
        color: $white;
        text-decoration: none;
        font-family: $font_one;
        padding: 15px 15px 14px;
        line-height: 1;
        font-size: 1.0em;
        margin: 0;
        display: block;
        font-weight: 400;
        text-align: center;
    }

    @content;
}

/************************************************\
* Tab Nav                                        *
\************************************************/
@mixin tab_nav() {
    margin-bottom: 50px;
    background-image: linear-gradient(0deg, rgba(214, 214, 214, 1) 0%, rgba(220, 220, 220, 1) 15%, rgba(255, 255, 255, 0.9) 80%);
    border-bottom: 1px solid $off-white;

    .tab_nav {
        display: flex;
        justify-content: flex-start;
        margin: 0 -5px;
        align-items: flex-end;
        flex-flow: row;

        &__wrap {
            margin-bottom: 0;
            display: flex;
            flex: 0 1 20%;
            padding: 0 5px;
            align-self: stretch;
        }

        &__single {
            width: 100%;

            &--active {
                @include hub_button(true);
            }

            &--inactive {
                @include hub_button();
            }
        }

        &__button {
        }
    }
}

/************************************************\
* Pagination                                     *
\************************************************/
@mixin pagination() {
    padding: 25px;
    width: 100%;

    text-align: center;

    .resource--pages {
        display: inline-block;
        padding: 0 15px;
    }

    ul.page-numbers {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            display: inline-block;
			margin: 0;

            .page-numbers {
                display: inline-block;
                text-align: center;
                text-decoration: none;
                border-radius: 2px;
                font-weight: 500;
                padding: 10px 20px 9px;
                font-size: 18px;
                border: 1px solid $teal;
                background-color: rgba($teal, 1);
                color: $white;
				margin: 0;

                &.next, &.prev {
                    margin: 0;
                }

                &:hover {
                    text-decoration: none;
                    background-color: rgba($teal, 0);
                    color: $teal;
                }

                &.current {
                    border: 1px solid rgba($teal, 1);
                    background-color: rgba($teal, 0);
                    color: $teal;
                    font-weight: 700;

                }
            }
        }
    }

    @content;
}