.resource_row {
    padding: 0;
}

.resource_card {
    box-shadow: $box-shadow;
    background-color: $white;
    height:100%;
    width: 100%;
    display: flex;

    &__wrap {
        width:100%;
        display: flex;
        flex-direction:column;
    }

    &__top {
        width:100%;

    }

    &__middle {
        padding: 20px 20px 10px;
        width:100%;
        flex-grow: 1;
    }

    &__bottom {
        padding: 10px 20px 20px;
        text-align: center;
    }

    &__img_wrap {
        background-color: $white;
        border-bottom: 1px solid #E7E7E7;

        &--contain {
            padding: 20px;

            .resource_card__ft_img {
                height: 135px;

                @media screen and (max-width: 1024px) {
                    height: 160px;
                }

                @media screen and (max-width: 768px) {
                    height: 135px;
                }

                @media screen and (max-width: 575px) {
                    height: 160px;
                }

                @media screen and (max-width: 400px) {
                    height: 110px;
                }
            }
        }
    }

    &__ft_img {
        background-repeat: no-repeat;
        height: 175px;

        @media screen and (max-width: 1024px) {
            height: 200px;
        }

        @media screen and (max-width: 768px) {
            height: 175px;
        }

        @media screen and (max-width: 575px) {
            height: 200px;
        }

        @media screen and (max-width: 400px) {
            height: 150px;
        }
    }

    &__type {
        font-size:  0.85em;
        font-weight: 600;
        color: $med-gray;
        text-transform: uppercase;
    }

    &__title {
        font-size: 1.125em;
        font-weight: 700;
        color: $blue;
        max-width: 100%;
        overflow-wrap: break-word;

        @media screen and (max-width: 575px) {
            min-height: initial !important;
        }
    }

    &__cta {
        @include button-style($white, $redorange, $redorange, 0.75);
        max-width:100%;

        a {
            padding: 5px 30px;
            max-width:100%;
            overflow-wrap: break-word;


            @media screen and (max-width: 400px) {
                width: 100%;
            }
        }
    }
}

.resource_type {
    &__section {
        width: 100%;
        background-color: $bluewhite;
        padding: 75px 0;
        margin: 0 !important;

        display:flex;
        align-items:center;
        justify-content: center;

        &:nth-child(odd) {
            background-color: $white;
        }
    }

    &__title {
        color: $blue;
        font-size: 1.9em;
        font-weight: 600;
        line-height: 1;
        text-align: center;
        margin-bottom: 1em;
    }

    &__cards {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: stretch;

        @media screen and (max-width: 575px) {
            margin: 0;
        }
    }
}