.resource {

    &--search {
        padding: 5% 0;
    }
    &--search-filters {
        margin-top: 0 !important;
        background-color: $teal;
        padding: 20px;
        border-radius: 1px;

    }

    &--search-form {
        color: $white;
        font-weight: 500;
    }

    &--search-bar {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;

        label {
            padding-right: 15px;
        }

        input {
            background-color: $white !important;
            border: 1px solid $white !important;
            border-radius: 1px !important;
            padding: 2px;
            color: $navy;
            max-width: 200px;
        }
    }

    &--filters {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
        padding-left: 15px !important;

        .resource--filter-text {
            padding-right: 15px;
            margin-right: 0 !important;

        }

        .resource--filter {
            margin-right: 15px;

            &:last-of-type {
                margin-right: 0;
            }

            select {
                background-color: $white !important;
                border: 1px solid $white !important;
                border-radius: 1px !important;
                padding: 2px;
                color: $navy;
                font-size: 16px;
                width: 175px;
                height: 30px;

            }
        }

    }

    &--search-row {

    }

    &--search-results {
        margin: 25px -15px !important;
        justify-content: center !important;
    }
}

//Tile styles
.resource {
    &--tile {
        flex: 1 1 $third;
        max-width: $third;
        padding: 15px;
    }

    &--tile-wrapper {
        border: 1px solid $border;
        border-radius: 1px;

    }

    &--link-wrapper {
        text-decoration: none !important;
    }

    &--tile-image-wrap {
        border-bottom: 1px solid $border;


        &__cover {
            .resource--tile-featured-img {
                background-size: cover;
            }
        }

        &__contain {
            padding: 20px 35px;
            .resource--tile-featured-img {
                background-size: contain;
                height: 160px;
            }
        }

    }

    &--tile-featured-img {
        height: 200px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center top;

        .screen-reader-text {
            position: absolute;
            opacity: 0;
        }
    }

    &--tile-inner-wrap {
        padding: 15px;
        position: relative;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        align-items: flex-start;

    }

    &--type {
        flex: 1 1 100%;
        font-weight: 500;
        color: $navy;
        font-size: 1.00em;
    }

    &--tile-title {
        flex: 1 1 100%;
        font-weight: 700;
        color: $navy;
        font-size: 1.25em;

    }

    &--tile-download {
        flex: 1 1 100%;
        text-align: right;


        color: $navy;
        text-decoration: none;
        text-transform: uppercase;
        font-family: $font_one;
        line-height: 1;
        font-size: 0.875em;
        margin: 0;
        font-weight: 400;
    }
}