$box-shadow: 0 1px 8px 0 rgba($dark-gray,0.25);
$boxshadow1 : 0 1px 8px 5px rgba(49,49,49,0.5);
$box-shadow2: 0 1px 8px 0 rgba($dark-gray,0.35);


/************************************************\
 * Break Points                                 *
\************************************************/

$breakpoints:				(
        'xx-small':			320px,
        'x-small':			480px,
        'small':			768px,
        'medium':			1024px,
        'large':			1200px,
        'x-large':			1400px,
        'xx-large':			1600px
);

/************************************************\
 * MATH                                         *
\************************************************/
$third: calc(100%/3);