.resource {

    &__layout {
        padding: 5% 0;

    }

    &__single_wrapper {

    }

    //Video Resource Items
    &__video_title {
        font-weight: 900;
        color: $navy;
    }

    &--content {

    }

    &--main-content {

    }

    &--content-wrapper {

        p {
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    &__noform_content {

    }

    &__main_ft_image {
        img {
            max-width: 100%;
            height: auto;
        }
    }

    //Main Resource Settings
    &__main_area {

    }

    &__video_wrap {

    }

    &--video-player {

    }

    &--video-wrapper {
        margin-bottom: 25px;
        iframe {

        }
    }

    &--video-info {

    }

    //Audio Resource Settings
    &--audio-player {
        line-height: 1;

    }

    &--audio-wrapper {
        line-height: 1;
        font-size: 0;
        margin-bottom: 15px;

        audio {
            width: 100%;
            line-height: 1;

        }
    }
}

.noform_sidebar {
    &__wrap {

    }

    &__download_button {
        @include button-style($white, $teal, $teal, .75);
        margin-top: 25px;
        margin-bottom: 0;
        text-align: center;
        display: block;

        a {

        }
    }
}


.single_resource {
    $width: calc(100% / 4 - 30px);
    flex: 1 1 $width;
    max-width: $width;
    padding: 15px;

    @media screen and (max-width: 1024px) {
        $width: calc(100% / 2 - 30px);
        flex: 1 1 $width;
        max-width: $width;
    }

    @media screen and (max-width: 575px) {
        flex: 1 1 100%;
        max-width: 100%;
        padding: 10px;
    }
}