.resource_banner
{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border-bottom: 1px solid $off-white;
    font-family: $font_one;
    padding-bottom: 2%;
    padding-top: 2%;
    background-color: #555459;
    margin: 0 !important;
    min-height:30vh;

    display:flex;
    align-items:center;
    justify-content: center;

    @media screen and (max-width: 1024px) {
        padding: 20px 15px;
    }

    &__title {
        text-align: center;
        justify-content: center;
        color: gray;
        margin-bottom: 0 !important;
        width: 100%;
        font-size: 250%;
        font-family: inherit;
        font-weight: 700;
    }

    &__subtitle {
        text-align: center;
        margin-bottom: 0 !important;
        font-size: 150%;
        Font-family: inherit;
        font-weight: 500;
    }

    &--resources {
        .blog_banner__subtitle {
            margin-top: 1em;
        }
    }
}